import React, { Component } from 'react';
import NavbarPage from '../components/Navbar';
import Sidebar from '../components/Sidebar';
import '../App.css';
import { Column } from 'simple-flexbox';
import { Row } from 'simple-flexbox';
import '../components/design.css';
import Rbox from '../components/recommendInput';
import { get_JWT } from '../components/API/jwt';
import axios from 'axios';

class Recommend extends Component {
    // constructor(props) {
    //     super(props);
    // }

    state = {
        recommend_list: [],
        loading: false,
        // heading: 'Top 10 Songs'
    };

    async componentDidMount() {
        window.addEventListener('resize', this.resize);
        const jwt = get_JWT();
        try {
            this.setState({loading: true});
            const res = await axios.get('https://us-central1-popfeed-268519.cloudfunctions.net/popfeed/users/' + jwt) // this gives a promise back
            console.log(res.data.recommend)
            this.setState({recommend_list: res.data.recommend, loading: false})
        }  catch (e) {
            console.log(e);
        }
    }

    render() {
        return (
            <Row className="mainContainer">
                <Sidebar />
                <Column flexGrow={1} className="mainBlock">
                    <NavbarPage />
                    <div className="gridContent">
                    <span className="navTitle"> Song Recomendations</span>
                        
                        <div className="grid">

                            <Rbox artist="Maroon 5" song="Sugar" date="14/1/2014"/>
                            <Rbox artist="Miley Cyris" song="Party In The Usa" date="25/9/2009"/>
                            <Rbox artist="Taylor Swift" song="Romeo and Juliet" date="16/6/2009"/>
                            <Rbox artist="Bruno Mars" song="Mary You" date="9/2/2017"/>
                            <Rbox artist="IU" song="Tell Me Why" date="21/4/2017"/>
                            <Rbox artist="BlackPink" song="As If Its Your Last" date="22/6/2017"/>
                            <Rbox artist="Black Eye Peas" song="Where Is The Love" date="1/1/1"/>
                            <Rbox artist="Dua Lipa" song="Don't Start Now" date="1/1/1"/>
                            <Rbox artist="Magic" song="Rude" date="1/1/1"/>

                        </div>
                    </div>
                </Column>
            </Row>
        );
    }
}

export default Recommend;