import React, { Component } from 'react';
import NavbarPage from '../components/Navbar';
import Sidebar from '../components/Sidebar';
import '../App.css';
// import 'bootstrap/dist/css/bootstrap.min.css';
import { Column } from 'simple-flexbox';
import { Row } from 'simple-flexbox';
import '../components/design.css';
import Sbox from '../components/songInput';

class Songs extends Component {

  render() {
    return (
        <Row className="mainContainer">
            <Sidebar />
            <Column flexGrow={1} className="mainBlock">
                <NavbarPage />
                <div className="gridContent">
                <span className="navTitle">Songs</span>
                    
                    <div className="grid">
                        <Sbox artist="Black Eye Peas" song="Where Is The Love" date="16/06/2009"/>
                        <Sbox artist="Dua Lipa" song="Don't Start Now" date="7/1/2019"/>
                        <Sbox artist="IU" song="Palette" date="21/3/2017"/>
                        <Sbox artist="BlackPink" song="As If Its Your Last" date="22/5/2017"/>
                        <Sbox artist="Magic" song="Rude" date="24/2/2013"/>
                        <Sbox artist="DaBaby" song="Toes" date="27/09/2019"/>


                    </div>
                </div>
            </Column>
        </Row>
    );
  }
}

export default Songs;
