import React from 'react';
import './design.css'
// import axios from 'axios';

const Userinput = (props) => {

    // if(props.id === "s")
    // {artrist}

    return (  
        <div className="gridBox">

            {
                (props.fav_id === "a")
                ? <div className="sidestatValue">Artist: {props.head}</div>
                : <div className="sidestatValue">Song: {props.head}</div>

            }
            {
                (props.fav_id === "s")
                ? <div className="sideStatTitle">Song: {props.sub}</div>
                : <div className="sideStatTitle">Artist: {props.sub}</div>
            }

            <div className="sideStatTitle">Date: {props.date}</div>

            <div className="addButton">Delete</div>

        </div>
    );
}



export default Userinput;