import React, { Component } from 'react';
import NavbarPage from '../components/Navbar';
import Sidebar from '../components/Sidebar';
import '../App.css';
// import 'bootstrap/dist/css/bootstrap.min.css';
import { Column } from 'simple-flexbox';
import { Row } from 'simple-flexbox';
import '../components/design.css'
import axios from 'axios';

class Login extends Component {

  constructor(props) {
    super(props)

    this.state = {
      fname: '',
      lname: '',
      email: '',
      password: '',
      cpassword: ''
  };

    this.change = this.change.bind(this);
    this.submit = this.submit.bind(this);

  }
  change(e) {
    this.setState({
        [e.target.name]: e.target.value
    })
  }

  submit(e) {
    e.preventDefault();
    const url = 'https://us-central1-popfeed-268519.cloudfunctions.net/popfeed/users/login';
    axios.post(url, {
        password: this.state.password,
        email: this.state.email
    })
    .then(res => {
        console.log(res); 
        localStorage.setItem('cool-jwt', res.data);
        this.props.history.push("/dashboard");

    })      
    .catch(err => console.log(err)) 
  }


  render() {
    return (
        <Row className="mainContainer">
            <Sidebar />
            <Column flexGrow={1} className="mainBlock">
                <NavbarPage />
                <div className="mainContent">
                <span className="navTitle">Profile</span>
                    {/* THe profile page for {this.props.match.params.username} */}

                    <div className="profileContainer">
                      <div className="profileBox">
                        <form onSubmit={e => this.submit(e)}>
                            <div className="loginInputGroup">
                              <label htmlFor="firstName">First Name</label>
                              <input name="fname" type="text" className="loginInput" placeholder="First Name" required value={this.state.fname} onChange={e =>this.change(e)}/>
                            </div>
                    
                            <div className="loginInputGroup">
                              <label htmlFor="lastName">Last Name</label>
                              <input name="lname" type="text" className="loginInput" placeholder="Last Name" value={this.state.lname} required onChange={e =>this.change(e)}/>
                            </div>

                            <div className="loginInputGroup">
                              <label htmlFor="email">Email</label>
                              <input name="email" type="text" className="loginInput" placeholder="Email" value={this.state.email} required onChange={e =>this.change(e)}/>
                            </div>

                            <div className="loginInputGroup">
                              <label htmlFor="password">Password </label>
                              <input name="password" type="password" className="loginInput" placeholder="Password" value={this.state.password} required onChange={e =>this.change(e)}/>
                            </div>
    
                            <div className="loginInputGroup">
                              <label htmlFor="password">Confirm Password</label>
                              <input name="cpassword" type="password" className="loginInput" placeholder="Password" value={this.state.cpassword} required onChange={e =>this.change(e)}/>
                            </div>

                            <button type="submit" className="loginButton" >Save</button>
                        </form>
                      </div>
                    </div>
                </div>
            </Column>
        </Row>
    );
  }
}

export default Login;
