import React, { Component } from 'react';
import NavbarPage from '../components/Navbar';
import Sidebar from '../components/Sidebar';
import '../App.css';
// import 'bootstrap/dist/css/bootstrap.min.css';
import { Column } from 'simple-flexbox';
import { Row } from 'simple-flexbox';
import '../components/design.css';
import MainComponent from '../components/MainContent';
import axios from 'axios';
// import Spinner from '../images/Spinner';
import { get_JWT } from '../components/API/jwt';
import { withRouter } from 'react-router-dom';

class dashboard extends Component {
    // constructor(props) {
    //     super(props);
    //   }

    componentWillUnmount() {
        window.removeEventListener('resize', this.resize);
    }

    resize = () => this.forceUpdate();

    state = {
        artist_list: [],
        loading: false,
        test: ["Bruno Mars", "Taylor Swift", "Kesha"]

    };
    // artist_list
    async componentDidMount() {
        window.addEventListener('resize', this.resize);

        const jwt = get_JWT();
        try {
            this.setState({loading: true});
            // const res = await axios.get('https://us-central1-popfeed-268519.cloudfunctions.net/popfeed/dashboard', {id: jwt}) // this gives a promise back
            const res = await axios.get('http://localhost:5000/dashboard?id=' + jwt) // this gives a promise back
            console.log(res.data)
            console.log("work")
            this.setState({artist_list: res.data, loading: false})
        }  catch (e) {
            console.log(e);
            // console.log("doesn't work")          
        }
        
    }

    render() {

    return (
        <Row className="mainContainer">
            <Sidebar />
            <Column flexGrow={1} className="mainBlock">
                <NavbarPage />

                <div className="mainContent">
                    <span className="multiTitle">Dashboard</span>
                    <MainComponent title="ariana grande" 
                        date="2020-03-04T17:14:24.900Z"
                        content="Music has always been Ariana Grande's first love. In fact, long before millions of fans fell in love with the singer and actress as Cat Valentine on the hit ..."
                        concertPrice="n/a n/a - n/a n/a"
                        albumName="thank u, next"
                        concert="No Events Found For ariana grande"
                        tweetID=""
                        youtubeID="9CA11SXv79Y"
                        youtubeTitle=""
                        youtubeChannel=""
                        artistSearch="ariana grande"
                        />
                    
                    <MainComponent title="bruno mars" 
                        date="2020-03-03T11:02:17.079Z"
                        content="Peter Gene Hernandez, known professionally as Bruno Mars, is an American singer, songwriter, record producer, multi-instrumentalist, and dancer. He is known ..."
                        concertPrice="91.28 USD - 505 USD"
                        albumName="Doo-Wops & Hooligans"
                        concert="Bruno Mars"
                        tweetID="1232755639522189312"
                        youtubeID=""
                        youtubeTitle=""
                        youtubeChannel=""
                        artistSearch="bruno mars"
                        />


                        {
                        (this.state.artist_list.length === 0) 
                        ? <div className="multiTitle">Artist Not Found</div> 
                        : (
                            <div> 
                                {this.state.artist_list.map(item => (
                                    <div>
                                    <MainComponent title={item.artist_name}
                                    date={item.createdAt}
                                    content={item.artist_description}                                   
                                    concertPrice={item.min + " " + item.currency + " - " + item.max + " " + item.currency}
                                    albumName={item.most_relevant_album}
                                    concert={item.latest_event}
                                    youtubeID={item.youtube_upload_id}
                                    youtubeTitle={item.youtube_upload_title}
                                    // youtubeChannel=""
                                    // artistSearch={item.artist_name}
                                    />    
                                    </div>
                                ))}


                            </div>
                        )
                    }
               
                </div>
            </Column>
        </Row>
    );
  }
}

export default withRouter(dashboard);