import React, { Component } from 'react';
import NavbarPage from '../components/Navbar';
import Sidebar from '../components/Sidebar';
import '../App.css';
import { Column } from 'simple-flexbox';
import { Row } from 'simple-flexbox';
import '../components/design.css';
import Fbox from '../components/favouritesInput';

class Favourites extends Component {

  render() {
    return (
        <Row className="mainContainer">
            <Sidebar />
            <Column flexGrow={1} className="mainBlock">
                <NavbarPage />
                <div className="gridContent">
                <span className="navTitle">Favourites</span>
                    
                    <div className="grid">

                        <Fbox head="Maroon 5" sub="Sugar" date="14/1/2014" fav_id="a"/>
                        <Fbox head="See You Again" sub="wiz khalifa" date="10/3/2015" fav_id="s"/>
                        <Fbox head="Despacito" sub=" Luis Fonsi" date="12/1/2017" fav_id="s"/>

                    </div>
                </div>
            </Column>
        </Row>
    );
  }
}



export default Favourites;
